*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.cardListHolder{
    margin:1rem;
    display: flex;
    flex-wrap: wrap;
    margin:0 5rem;
}
.cardHolder{
    border:2px solid rgb(197, 195, 195);
    width: 20rem;
    height: 30rem;
    margin: .5rem;
    border-radius: 25px;
    overflow: hidden;
}
.imageSection{
    height:60%;
    background-color:#f5f5f5;
}
.imageSection> img{
    width: 100%;
    height: 100%;
}
.detailsSection {
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding-left: 1rem;
}
.detailsSection>div{
    display: flex;
    align-items: center;
}
.icons{
    margin-right: .5rem;
}
a{
    text-decoration: none;
    color:black
}
.footerSection{
    height:10%;
    background-color:#f5f5f5;
}
.footerSection ul{
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
}
.icon:hover,button:hover{
    cursor: pointer;
}
.like{
    color: red;
}
.edit,.delete{
    color: grey;
}
.delete:hover{
    color:blue;
    transition: .5s;
}
.formPage{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000;
    /* display:block; */
    overflow: auto;
    outline: 0;
    background-color: rgba(256, 256, 256, .70);
}
.formHolder{
    position: relative;
    top:12%;
    width: 35rem;
    height:23rem;
    background-color: #fff;
    background-clip: padding-box;
    border: 0;
    margin: 0 auto;
    border-radius: 25px;
    box-shadow: 0 4px 12px rgb(0 0 0 / 15%);
}
.form{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.form input,button{
    width:55%;
    font-size: 18px;
    margin:.6rem;
    border-radius: 5px;
    outline: none;
    border: 1px solid black;
    padding:.2rem;
}
.clrRed{
    color: red;
}
.close{
    position: relative;
    left:43%;
    top:1rem
}